.header {
  padding: 31px 79px 30px 40px;
  background-color: $color-secondary-light;
  &__logo {
    width: 201px;
    height: 50px;
    .logo {
      width: 100%;
    }
  }
  @media all and (min-width: 768px) {
    padding-right: 527px;
  }
  @media all and (min-width: 1024px) {
    padding-right: 783px;
  }
}
